/* eslint-disable react/prop-types */
import React from 'react'
import { Image } from 'antd'
import styled from 'styled-components'

const ImageStyled = styled(Image)`
  display: flex;
  align-items: center;
`

const Img = ({ src, ...rest }) => (
  <ImageStyled
    {...rest}
    src={src}
    preview={false}
    fallback="data:image/png;base64,"
  />
)

export default Img
